/*--------------------------------------------------------------
# Inner Circle Event Cards

1. Mobile
2. Desktop
--------------------------------------------------------------*/
/* 
Media queries modeled on those found in Bootstrap.
See: https://getbootstrap.com/docs/4.5/layout/overview/#responsive-breakpoints for context.
*/
#events .col {
  margin-bottom: 1rem; }

#events .card-ic-event .card-header {
  padding: 1rem 1.5rem; }

#events .card-ic-event .card-details {
  padding: 1rem 1.5rem; }
  #events .card-ic-event .card-details svg {
    margin-right: 0.5rem; }

/*--------------------------------------------------------------
2. Desktop
--------------------------------------------------------------*/
@media (min-width: calc(992px)) {
  #events .col {
    margin-bottom: 2rem; }
  #events .card-ic-event {
    display: grid;
    grid-template-columns: 3fr 2fr;
    gap: 0px 0px; }
    #events .card-ic-event .card-header {
      grid-column: 1 / span 1;
      padding: 1rem 1.5rem 2rem 1.5rem; }
    #events .card-ic-event .card-details {
      grid-column: 2 / span 1;
      padding-left: 0;
      padding-top: 2rem; } }

/*--------------------------------------------------------------
# Inner Circle - Single Post

1. Mobile
2. Desktop
--------------------------------------------------------------*/
/* 
Media queries modeled on those found in Bootstrap.
See: https://getbootstrap.com/docs/4.5/layout/overview/#responsive-breakpoints for context.
*/
/*--------------------------------------------------------------
1. File Attachment (.downloads)
--------------------------------------------------------------*/
.downloads {
  position: relative;
  margin: 3rem 0;
  padding-top: 3rem; }
  .downloads:before {
    content: '';
    height: 1px;
    width: 100%;
    max-width: 16rem;
    display: block;
    position: absolute;
    top: 0;
    background-color: #d0d0d0; }
  .downloads img {
    margin-right: 1.5rem; }
  .downloads img.attachment-icon {
    padding: 1.5rem;
    border: 1px solid #e8e8e8; }
  .downloads .media-body p:first-child {
    font-weight: 700; }

/*--------------------------------------------------------------
2. IC Story Hero, Mobile
--------------------------------------------------------------*/
.uds-story-hero .content .breadcrumb,
.uds-story-hero .content p.meta {
  padding: .75rem 0;
  margin: 0.25rem 0; }

.uds-story-hero .content h1 {
  margin: 2rem 0 0.25rem; }

/*--------------------------------------------------------------
3. IC Story Hero, Desktop
--------------------------------------------------------------*/
@media (min-width: 950px) {
  .content .breadcrumb,
  .content p.meta {
    margin: 1.25rem 0 2.25rem; } }

/*--------------------------------------------------------------
# Inner Circle - Tag.php

1. Mobile
2. Desktop
--------------------------------------------------------------*/
/* 
Media queries modeled on those found in Bootstrap.
See: https://getbootstrap.com/docs/4.5/layout/overview/#responsive-breakpoints for context.
*/
/*--------------------------------------------------------------
1. Mobile
--------------------------------------------------------------*/
.archive.tag #tag-hero {
  position: relative;
  min-height: 400px; }
  .archive.tag #tag-hero .term-description {
    font-size: 1.25rem;
    padding-right: 2rem; }
  .archive.tag #tag-hero svg {
    position: absolute;
    top: 0; }
  .archive.tag #tag-hero img {
    margin-top: 1rem;
    border-radius: 20px; }

.archive.tag article {
  border-bottom: 1px solid #e4e4e4;
  padding-bottom: 1rem;
  margin-bottom: 1rem; }
  .archive.tag article:last-child {
    border-bottom: 0; }

.archive.tag .entry-footer {
  display: inline-block; }
  .archive.tag .entry-footer svg {
    margin-right: 0.5rem; }
  .archive.tag .entry-footer svg.fa-clock, .archive.tag .entry-footer svg.fa-map-marker-alt {
    margin-left: 1.5rem; }
  .archive.tag .entry-footer .card-tags {
    margin-top: 1rem;
    padding: 0; }

/*--------------------------------------------------------------
2. Desktop
--------------------------------------------------------------*/
@media (min-width: calc(992px)) {
  .archive.tag article {
    padding-bottom: 4rem;
    margin-bottom: 2rem; } }

/*--------------------------------------------------------------
# Inner Circle Post Group Block

1. Mobile
2. Desktop, LG
3. Desktop, XL
--------------------------------------------------------------*/
/* 
Media queries modeled on those found in Bootstrap.
See: https://getbootstrap.com/docs/4.5/layout/overview/#responsive-breakpoints for context.
*/
/*--------------------------------------------------------------
1. Mobile
--------------------------------------------------------------*/
.ic-post-group {
  position: relative;
  padding-top: 1rem; }
  .ic-post-group:before {
    content: '';
    height: 1rem;
    width: calc(100% - 2rem);
    background-color: #ffc627;
    position: absolute;
    left: 1rem;
    top: 0; }
  .ic-post-group img {
    width: 100%;
    height: auto; }
  .ic-post-group a {
    padding: 0.5rem 1rem;
    display: block;
    text-decoration: none;
    color: #191919; }
    .ic-post-group a h4 {
      text-decoration: underline;
      color: #8c1d40; }
  .ic-post-group .story {
    padding: 0.5rem;
    border-bottom: 1px solid #d0d0d0; }
    .ic-post-group .story .event-meta svg {
      margin-right: 0.5rem; }

/*--------------------------------------------------------------
2. Desktop, LG
--------------------------------------------------------------*/
@media (min-width: calc(992px)) {
  .ic-post-group {
    display: grid;
    grid-template-columns: 20rem 1fr 1.5rem;
    grid-template-rows: 1.5rem 1fr 1.5rem;
    gap: 0px 0px; }
    .ic-post-group:before {
      display: none; }
    .ic-post-group img {
      width: 100%;
      height: auto;
      grid-row: 1 / span 3;
      grid-column: 2; }
    .ic-post-group .story-wrap {
      grid-row: 2;
      grid-column: 1; }
      .ic-post-group .story-wrap .story {
        background-color: #e8e8e8;
        margin: 0 0 1rem 1.5rem;
        border-bottom: 0.25rem solid #ffffff;
        transition: 0.5s cubic-bezier(0.19, 1, 0.19, 1); }
        .ic-post-group .story-wrap .story h4 {
          margin: 0 auto .25rem;
          color: #191919;
          text-decoration: none; }
        .ic-post-group .story-wrap .story .event-meta {
          margin-bottom: 0; }
        .ic-post-group .story-wrap .story a {
          color: #191919;
          text-decoration: none; }
        .ic-post-group .story-wrap .story:hover {
          margin-left: 0; }
          .ic-post-group .story-wrap .story:hover h4 {
            text-decoration: underline; }
        .ic-post-group .story-wrap .story.active {
          background-color: #191919;
          color: #ffffff;
          border-color: #ffc627;
          margin-left: 0; }
          .ic-post-group .story-wrap .story.active a, .ic-post-group .story-wrap .story.active h4 {
            color: #ffffff; }
        .ic-post-group .story-wrap .story:last-child {
          margin-bottom: 0; }
        .ic-post-group .story-wrap .story:nth-child(3) {
          display: none; }
    .ic-post-group:after {
      content: '';
      width: 1.5rem;
      background-color: #ffc627;
      grid-column: 3;
      grid-row: 2; }
  .ic-post-group.reversed {
    grid-template-columns: 1.5rem 1fr 28rem; }
    .ic-post-group.reversed:after {
      grid-column: 1; }
    .ic-post-group.reversed img {
      grid-column: 2; }
    .ic-post-group.reversed .story-wrap {
      grid-column: 3; }
      .ic-post-group.reversed .story-wrap .story {
        margin: 0 1.5rem 1rem 0; }
        .ic-post-group.reversed .story-wrap .story:hover {
          margin-right: 0;
          padding-left: 1.5rem; }
        .ic-post-group.reversed .story-wrap .story.active {
          margin-right: 0;
          padding-left: 1.5rem; } }

/*--------------------------------------------------------------
3. Desktop, XL
--------------------------------------------------------------*/
@media (min-width: calc(1224px)) {
  .ic-post-group {
    grid-template-columns: 28rem 1fr 1.5rem; }
    .ic-post-group .story-wrap .story:nth-child(3) {
      display: block; }
  .ic-post-group.reversed {
    grid-template-columns: 1.5rem 1fr 28rem; } }

